import { ICompanyDetail } from '../../../interfaces';
import ButtonLink from '../../atoms/ButtonLink';
import LogoAndName from './LogoAndName';
import VacancyList from './VacancyList';

interface ICompanyProfileProps {
  company: ICompanyDetail;
}

export default function VacancyCard(props: ICompanyProfileProps) {
  const { company } = props;
  return (
    <div className="mb-5 w-full font-primary lg:mb-10">
      <div className="flex flex-col justify-between lg:flex-row">
        <LogoAndName company={company} />
        <div>
          <ButtonLink
            display="block"
            href={`/booth/${company.id}`}
            className="mt-3 text-white lg:mt-0"
          >
            Company Booth
          </ButtonLink>
        </div>
      </div>
      <div className="mt-3">
        <VacancyList company={company} />
      </div>
    </div>
  );
}
