import { IVacancyCard } from '../../../interfaces';
import ButtonLink from '../../atoms/ButtonLink';

interface Props {
  vacancy: IVacancyCard | undefined;
  index: number;
}

export default function VacancyItem(props: Props) {
  const { vacancy, index } = props;
  return (
    <li className="mb-2 flex items-center justify-between border-b border-primary/50 px-2 pb-2 hover:border-b-2 hover:border-primary">
      <p>
        <span className="mr-2 inline-block font-mono">{index}</span>
        <span>{vacancy?.title}</span>
      </p>
      <span>
        <ButtonLink
          href={vacancy?.link!}
          isNativeAnchor
          color="warning"
          display="block"
          className="flex-shrink-0 text-gray-800"
        >
          Lamar
        </ButtonLink>
      </span>
    </li>
  );
}
