import { ICompanyDetail } from '../../../interfaces';

interface ICompanyProfileProps {
  company: ICompanyDetail;
}

export default function LogoAndName(props: ICompanyProfileProps) {
  const { company } = props;
  return (
    <div className="flex gap-4">
      <img
        src={company.image}
        className="block w-36 rounded-lg"
        alt={company.name}
      />
      <div>
        <h4 className="text-2xl font-semibold">{company.name}</h4>
        <p>{company.address}</p>
      </div>
    </div>
  );
}
