import { ICompanyDetail } from '../../../interfaces';
import VacancyItem from './VacancyItem';

interface ICompanyProfileProps {
  company: ICompanyDetail;
}

export default function VacancyList(props: ICompanyProfileProps) {
  const { company } = props;
  return (
    <ol>
      {company?.vacancies?.map((vacancy, index) => (
        <VacancyItem key={vacancy.id} index={index + 1} vacancy={vacancy} />
      ))}
    </ol>
  );
}
